'use client';

import React from 'react';
import { GoogleMap, LoadScript, Marker } from '@react-google-maps/api';

const containerStyle = {
  width: '100%',
  height: '600px',
  borderRadius: '0.5rem',
};

const center = {
  lat: 37.7749,
  lng: -122.4194,
};

// Sample incident data
const incidents = [
  { id: 1, lat: 37.78, lng: -122.42, category: 'Water Logging' },
  { id: 2, lat: 37.77, lng: -122.41, category: 'Accident' },
  { id: 3, lat: 37.76, lng: -122.43, category: 'Water Logging' },
  { id: 4, lat: 37.79, lng: -122.4, category: 'PowerCut' },
  { id: 5, lat: 37.75, lng: -122.45, category: 'Water Logging' },
  { id: 6, lat: 37.8, lng: -122.44, category: 'Accident' },
  { id: 7, lat: 37.785, lng: -122.405, category: 'PowerCut' },
];

interface MapComponentProps {
  showWaterLogging: boolean;
  showAccidents: boolean;
  showPowerCuts: boolean;
}

export function MapComponent({ showWaterLogging, showAccidents, showPowerCuts }: MapComponentProps) {
  const apiKey = process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY;

  if (!apiKey) {
    return (
      <div className="flex items-center justify-center h-96 bg-muted rounded-lg">
        <p className="text-destructive">
          Google Maps API key is missing. Please add it to your .env file.
        </p>
      </div>
    );
  }

  const filteredIncidents = incidents.filter(incident => {
    if (showWaterLogging && incident.category === 'Water Logging') {
      return true;
    }
    if (showAccidents && incident.category === 'Accident') {
      return true;
    }
    if (showPowerCuts && incident.category === 'PowerCut') {
      return true;
    }
    return false;
  });

  return (
    <LoadScript googleMapsApiKey={apiKey}>
      <GoogleMap mapContainerStyle={containerStyle} center={center} zoom={12}>
        {filteredIncidents.map(incident => (
          <Marker key={incident.id} position={{ lat: incident.lat, lng: incident.lng }} />
        ))}
      </GoogleMap>
    </LoadScript>
  );
}
