'use client';

import { useState } from 'react';
import { MapComponent } from '@/components/map';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Label } from '@/components/ui/label';
import { Switch } from '@/components/ui/switch';
import { Droplets, Car, PowerOff } from 'lucide-react';

export default function MapPage() {
  const [showWaterLogging, setShowWaterLogging] = useState(true);
  const [showAccidents, setShowAccidents] = useState(false);
  const [showPowerCuts, setShowPowerCuts] = useState(false);

  return (
    <div className="bg-background min-h-screen">
      <main className="container mx-auto px-4 py-8 md:py-16">
        <header className="text-center mb-12">
          <h1 className="text-4xl sm:text-5xl font-bold font-headline text-primary">Incidents Map</h1>
          <p className="mt-4 text-lg text-muted-foreground">Visualize reported incidents on the map.</p>
        </header>
        <div className="relative">
          <MapComponent 
            showWaterLogging={showWaterLogging} 
            showAccidents={showAccidents}
            showPowerCuts={showPowerCuts}
          />
          <Card className="absolute top-4 left-4 z-10 w-64 shadow-lg">
            <CardHeader>
              <CardTitle className="text-lg">Map Filters</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="flex items-center space-x-3">
                <Droplets className="w-5 h-5 text-primary" />
                <Label htmlFor="water-logging-switch" className="flex-grow">Show Water Logging</Label>
                <Switch
                  id="water-logging-switch"
                  checked={showWaterLogging}
                  onCheckedChange={setShowWaterLogging}
                />
              </div>
              <div className="flex items-center space-x-3">
                <Car className="w-5 h-5 text-primary" />
                <Label htmlFor="accidents-switch" className="flex-grow">Show Accidents</Label>
                <Switch
                  id="accidents-switch"
                  checked={showAccidents}
                  onCheckedChange={setShowAccidents}
                />
              </div>
              <div className="flex items-center space-x-3">
                <PowerOff className="w-5 h-5 text-primary" />
                <Label htmlFor="power-cuts-switch" className="flex-grow">Show Power Cuts</Label>
                <Switch
                  id="power-cuts-switch"
                  checked={showPowerCuts}
                  onCheckedChange={setShowPowerCuts}
                />
              </div>
            </CardContent>
          </Card>
        </div>
      </main>
    </div>
  );
}
